import {
	mapGetters
} from 'vuex';
export default {
	name: 'ltabbar',
	computed: {
		...mapGetters(['ltabbar'])
	},
	data() {
		return {
			active: 'home'
		};
	},
	methods: {
		onChange(event) {
			const e = event.detail;
			const s = this.$store
			if (e === 'home') {
				uni.switchTab({
					url: '/pages/index/index'
				});
			} else if (e === 'job') {
				uni.switchTab({
					url: '/pages/job/index'
				});
			} else if (e === 'resume') {
				if (s.getters.register) {
					if (s.getters.member.resume) {
						uni.switchTab({
							url: '/pages/resume/index'
						});
					} else {
						uni.navigateTo({
							url: '/pages/resume/basic'
						});
					}
				} else {






					uni.navigateTo({
						url: '/pages/login/wx'
					});

				}
			} else if (e === 'company') {
				uni.switchTab({
					url: '/pages/company/index'
				});
			} else if (e === 'user') {
				if (s.getters.register) {
					uni.switchTab({
						url: '/pages/my/index'
					});
				} else {






					uni.navigateTo({
						url: '/pages/login/wx'
					});

				}
			}
		}
	}
};
